const regexURL=/https?:\/\/([a-zA-Z0-9]([^ @&%$\\\/()=?¬ø!.,:;]|\d)*[a-zA-Z0-9][\.])+[a-zA-Z]{2,4}([\.][a-zA-Z]{2})?\/?((?<=\/)(([^ @&$#\\\/()+=?¬ø!,:;'&quot;^¬¥*%|]|\d)+[\/]?)*(#(?<=#)[^ @&$#\\\/()+=?¬ø!,:;'&quot;^¬¥*%|]*)?(\?(?<=\?)([^ @&$#\\\/()+=?¬ø!,:;'&quot;^¬¥*|]+[=][^ @&$#\\\/()+=?¬ø!,:;'&quot;^¬¥*|]+(&(?<=&)[^ @&$#\\\/()+=?¬ø!,:;'&quot;^¬¥*|]+[=][^ @&$#\\\/()+=?¬ø!,:;'&quot;^¬¥*|]+)*))?)?/

export default function validation(inputs){
    let errors={};

    //Valida nombre:
    if(inputs.name.length>40){
        errors.name="el nombre del videogame debe ser de m√°ximo 40 caracteres"
    };

    //Valida Descripci√≥n:
    if(inputs.description.length>240){
        errors.description="La descripci√≥n del videogame debe ser de m√°ximo 240 caracteres"
    };

    //Validaci√≥n de fechas:
    const dateNow = new Date(Date.now())
    const dateRelease = new Date(`${inputs.released}T00:00:00`)

    if(inputs.released!=="" && dateRelease>dateNow){
        errors.released="La fecha de lanzamiento no puede ser un valor futuro"
    };

    //Validaci√≥n de Rating
    if(inputs.rating<0 || inputs.rating>5){
        errors.rating="El rating debe ser un n√∫mero entre 0 y 5 (puede incluir decimales)"
    };

    //Valiaci√≥n de Brackground_image (URL)
    if(inputs.background_image!=="" && !regexURL.test(inputs.background_image)){
        errors.background_image="Debe ingresar una URL v√°lida ej:'http://www.miimagen.com/abcd'"
    };
    
    return errors;
}